import * as _ from 'lodash';
import { nav, routes } from 'router';


class Session {
  STORAGE_KEY = 'mofkra_storage_auth';

  saveUser = (data) => {
    if (!('token' in data))
      throw "Missing 'token' key in data";

    if (!('user_type' in data))
      throw "Missing 'user_type' key in data";

    // Convert snake case to camel case
    data = _.mapKeys(data, (value, key) => _.camelCase(key));

    // data.userType = data['user_type'];
    // data.userType = data['user_type'];

    let key = `${this.STORAGE_KEY}_user`;
    localStorage.setItem(key, JSON.stringify(data));
  };

  currentUser = () => {
    let key = `${this.STORAGE_KEY}_user`;
    return JSON.parse(localStorage.getItem(key));
  };

  removeUser = () => {
    let key = `${this.STORAGE_KEY}_user`;
    localStorage.removeItem(key);
  };

  logout = () => {
    this.removeUser();
    nav(routes.home);
  };

  isAuthenticated = () => this.currentUser() !== null;
  isTeacher = () => {
    let currentUser = this.currentUser();
    return !!(currentUser && currentUser.userType === 'teacher');
  };

  isStudent = () => {
    let currentUser = this.currentUser();
    return !!(currentUser && currentUser.userType === 'student');
  };
}

export default new Session();
