import React from 'react';
import { LoadingSpinner, PlaceholderText, ImageIcon } from 'components/atoms';
import { ClassCard, JoinClassModal } from 'components/molecules';


const StudentClasses = ({ isJoinClassModalOpen, openJoinClassModalState, joinClass,
                          joinClassLoading, joinClassError, closeJoinClassModalState,
                          classesLoading, classes }) => {

  return (
    <div className="page-content-block container">
      <JoinClassModal
        isOpen={isJoinClassModalOpen}
        closeModal={closeJoinClassModalState}
        joinClass={joinClass}
        joinClassLoading={joinClassLoading}
        joinClassError={joinClassError}
      />

      <div className="classes">
        <h4 className="block-title text-right"><ImageIcon name="class" width="50px" />صفوفي
          <button
            type="button"
            className="btn btn-outline-primary btn-add-class"
            onClick={openJoinClassModalState}>
            انضم إلى صف جديد
          </button>
        </h4>

        <div className="classes-container">
          <div className="row">
            {classesLoading ? (
              <LoadingSpinner large />
            ) : (
              classes.length === 0 ? (
                <PlaceholderText text='لا يوجد لديك صفوف!' />
              ) : (
                classes.map((class_, index) => (
                  <div className="col-sm-6" key={`${class_.id}_${index}`}>
                    <ClassCard class_={class_} />
                  </div>
                ))
              )
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default StudentClasses;
