import React from 'react';
import PropTypes from 'prop-types';


const Link = ({ className, onClick, underline, children }) => {
  return (
    <span className={`text-link ${underline ? 'underline' : ''} ${className}`} onClick={onClick}>
      {children}
    </span>
  );
};

Link.propTypes = {
  className: PropTypes.string,
  underline: PropTypes.bool,
  onClick: PropTypes.func
};

Link.defaultProps = {
  className: '',
  underline: true
};

export default Link;
