import React from 'react';
import PropTypes from 'prop-types';
import TaskCard from '../TaskCard/TaskCard';
import { PlaceholderText } from 'components/atoms';


const DateTasks = ({ tasks, date, showTitle, showClass, toggleTaskCompletion,
                     deleteClassTask, openEditTaskModal }) => {
  return (
    <div className="date-tasks-group w-100">
      {showTitle ? (
        <h5 className="tasks-date-title"><i className="fas fa-calendar-day"></i> مهام/واجبات تاريخ {date}</h5>
      ) : null}

      {tasks.length === 0 ? (
        <PlaceholderText text='لا يوجد مهام/واجبات لهذا التاريخ.' />
      ) : (
        tasks.map((task, index) =>
          <TaskCard
            task={task}
            showClass={showClass}
            toggleTaskCompletion={toggleTaskCompletion}
            deleteClassTask={deleteClassTask}
            openEditTaskModal={openEditTaskModal}
            key={`${task.id}_${index}`}
          />)
      )}
    </div>
  );
};

DateTasks.propTypes = {
  tasks: PropTypes.array.isRequired,
  date: PropTypes.string,
  showTitle: PropTypes.bool,
  showClass: PropTypes.bool,
  toggleTaskCompletion: PropTypes.func
};

DateTasks.defaultProps = {
  date: '',
  showTitle: true,
  showClass: false
};

export default DateTasks;
