import * as constants from './constants';
import { find } from 'lodash';


const INITIAL_STATE = {
  class_: {},

  classTasksLoading: false,
  classTasksByDate: {},

  classStudentsLoading: false,
  classStudents: [],

  taskSubmitted: false,
  editClassTask: {},
};


const getClassTaskByID = (tasksByDate, taskID) =>
  find(Object.values(tasksByDate).flat(1), ['id', taskID]);

