export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case constants.TEACHER_CLASS_FETCH_SUCCESS:
      return { ...state, class_: payload };


    case constants.TEACHER_CLASS_TASKS_FETCH_START:
      return { ...state, classTasksLoading: true };

    case constants.TEACHER_CLASS_TASKS_FETCH_SUCCESS:
      return { ...state, classTasksLoading: false, classTasksByDate: payload };

    case constants.TEACHER_CLASS_TASKS_FETCH_FAILURE:
      return { ...state, classTasksLoading: false };


    case constants.TEACHER_CLASS_STUDENTS_FETCH_START:
      return { ...state, classStudentsLoading: true };

    case constants.TEACHER_CLASS_STUDENTS_FETCH_SUCCESS:
      return { ...state, classStudentsLoading: false, classStudents: payload };

    case constants.TEACHER_CLASS_STUDENTS_FETCH_FAILURE:
      return { ...state, classStudentsLoading: false };


    case constants.CLASS_TASK_CREATE_SUCCESS:
      return { ...state, taskSubmitted: true };

    case constants.CLASS_TASK_UPDATE_SUCCESS:
      // return { ...state, taskSubmitted: true, classTasksByDate: updateClassTask(state.classTasksByDate, payload) };
      return { ...state, taskSubmitted: true };


    case constants.CLASS_TASK_EDIT_SET:
      return { ...state, editClassTask: getClassTaskByID(state.classTasksByDate, payload) };

    case constants.CLASS_TASK_EDIT_UNSET:
      return { ...state, editClassTask: {} };

    default:
      return state;
  }
}
