import React from 'react';
import PropTypes from 'prop-types';
import session from 'services/session';
import StudentClassCard from './StudentClassCard';
import TeacherClassCard from './TeacherClassCard';


const ClassCard = ({ class_, deleteClass }) => {
  let isTeacher = session.isTeacher();
  if (isTeacher)
    return <TeacherClassCard class_={class_} deleteClass={deleteClass} />;

  return <StudentClassCard class_={class_} />;
};

ClassCard.propTypes = {
  class_: PropTypes.object.isRequired,
  deleteClass: PropTypes.func
};


export default ClassCard;
