import { SubmissionError } from 'redux-form';
import api from 'services/api';
import session from 'services/session';
import { parseErrorResponse, redirectAfterAuth } from 'utils/helpers';


export const registerTeacher = (data) => {
  return dispatch => {
    let _data = { ...data };

    _data['email'] = _data['email'].trim();
    _data['username'] = _data['username'].trim();

    let [firstName, lastName] = _data['name'].trim().split(' ').filter(s => s);
    _data['first_name'] = firstName;
    _data['last_name'] = lastName;

    return api.registerTeacher(_data)
      .then(rsp => {
        session.saveUser(rsp.data);
        redirectAfterAuth();
      })
      .catch(error => {
        throw new SubmissionError(error.response ? error.response.data : {
          '_error': 'حدث خطأ في التسجيل، الرجاء المحاولة لاحقاً'
        });
      });
  };
};


export const registerStudent = (data) => {
  return dispatch => {
    let _data = { ...data };

    _data['username'] = _data['username'].trim();

    let [firstName, lastName] = _data['name'].trim().split(' ').filter(s => s);
    _data['first_name'] = firstName;
    _data['last_name'] = lastName;

    if (_data.code)
      _data.code = _data.code.trim();

    return api.registerStudent(_data)
      .then(rsp => {
        session.saveUser(rsp.data);
        redirectAfterAuth();
      })
      .catch(error => {
        throw new SubmissionError(error.response ? error.response.data : {
          '_error': 'حدث خطأ في التسجيل، الرجاء المحاولة لاحقاً'
        });
      });
  };
};


export const login = (data) => {
  return dispatch => {
    data.username = data.username.trim();

    // It doesn't matter studentAPI or teacherAPI, they have the same login API call
    return api.login(data)
      .then(rsp => {
        session.saveUser(rsp.data);
        redirectAfterAuth();
      })
      .catch(error => {
        throw new SubmissionError(error.response ? parseErrorResponse(error.response) : {
          '_error': 'حدث خطأ في تسجيل الدخول، الرجاء المحاولة لاحقاً'
        });
      });
  };
};
