import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  fetchTeacherClasses,
  deleteClass,
  setEditClass,
  unsetEditClass
} from 'store/actions';
import TeacherClasses from 'components/organisms/TeacherClasses/TeacherClasses';


class TeacherClassesContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isClassModalOpen: false
    };
  }

  componentDidMount() {
    this.props.fetchClasses();
  }

  openClassModal = () => {
    this.setState({ ...this.state, isClassModalOpen: true });
  };

  closeClassModal = () => {
    this.props.unsetEditClass();
    this.setState({ ...this.state, isClassModalOpen: false });
  };

  openEditClassModal = classID => {
    this.props.setEditClass(classID);
    this.openClassModal();
  };

  render() {
    const { isClassModalOpen } = this.state;
    const { classes, classesLoading, deleteClass } = this.props;

    return (
      <TeacherClasses
        classesLoading={classesLoading}
        classes={classes}
        deleteClass={deleteClass}

        isClassModalOpen={isClassModalOpen}
        openClassModal={this.openClassModal}
        openEditClassModal={this.openEditClassModal}
        closeClassModal={this.closeClassModal}
      />
    );
  }
}


const mapDispatchToProps = dispatch => ({
  fetchClasses: () => dispatch(fetchTeacherClasses()),
  deleteClass: classID => dispatch(deleteClass(classID)),
  setEditClass: classID => dispatch(setEditClass(classID)),
  unsetEditClass: () => dispatch(unsetEditClass())
});

const mapStateToProps = state => ({
  classesLoading: state.teacherClasses.classesLoading,
  classes: state.teacherClasses.classes
});


TeacherClassesContainer = connect(mapStateToProps, mapDispatchToProps)(TeacherClassesContainer);

export default TeacherClassesContainer;
