import { SHOW_ALERT, HIDE_ALERT } from 'store/constants';


export const showAlert = (message, type) => {
  return (dispatch) => {
    dispatch({ type: SHOW_ALERT, payload: { message, type } })
  }
};

export const hideAlert = () => {
  return (dispatch) => {
    dispatch({ type: HIDE_ALERT })
  }
};
