import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  fetchTeacherClass,
  fetchTeacherClassTasks,
  fetchTeacherClassStudents,
  deleteClassTask,
  setEditClassTask,
  unsetEditClassTask
} from 'store/actions';
import CMSClassPage from 'pages/CMSClassPage/CMSClassPage';


class CMSClassPageContainer extends Component {
  constructor(props) {
    super(props);

    this.classID = this.props.match.params.id;

    this.state = {
      activeTab: 'tasks',
      isTaskModalOpen: false,
    };
  }

  componentDidMount() {
    const {
      fetchTeacherClass,
      fetchTeacherClassTasks,
      fetchTeacherClassStudents
    } = this.props;

    fetchTeacherClass(this.classID);
    fetchTeacherClassTasks(this.classID);
    fetchTeacherClassStudents(this.classID);
  }

  changeActiveTab = (tab) => {
    this.setState({ ...this.state, activeTab: tab });
  }

  openTaskModal = () => this.setState({ ...this.state, isTaskModalOpen: true });

  openEditTaskModal = taskID => {
    this.props.setEditClassTask(taskID);
    this.openTaskModal();
  }

  closeTaskModal = () => {
    this.props.unsetEditClassTask();
    this.setState({ ...this.state, isTaskModalOpen: false });
  };

  render() {
    const { class_, classTasksByDate, classTasksLoading,
      classStudentsLoading, classStudents, deleteClassTask } = this.props;
    const { activeTab, isTaskModalOpen } = this.state;

    return (
      <CMSClassPage
        class_={class_}
        classTasksLoading={classTasksLoading}
        classTasksByDate={classTasksByDate}

        activeTab={activeTab}
        changeActiveTab={this.changeActiveTab}

        isTaskModalOpen={isTaskModalOpen}
        openTaskModal={this.openTaskModal}
        openEditTaskModal={this.openEditTaskModal}
        closeTaskModal={this.closeTaskModal}

        classStudentsLoading={classStudentsLoading}
        classStudents={classStudents}

        deleteClassTask={deleteClassTask}
      />
    );
  }
}


const mapDispatchToProps = dispatch => ({
  fetchTeacherClass: classID => dispatch(fetchTeacherClass(classID)),
  fetchTeacherClassTasks: classID => dispatch(fetchTeacherClassTasks(classID)),
  fetchTeacherClassStudents: classID => dispatch(fetchTeacherClassStudents(classID)),
  deleteClassTask: (classID, taskID) => dispatch(deleteClassTask(classID, taskID)),
  setEditClassTask: taskID => dispatch(setEditClassTask(taskID)),
  unsetEditClassTask: () => dispatch(unsetEditClassTask()),
});

const mapStateToProps = state => ({
  class_: state.classCms.class_,

  classTasksLoading: state.classCms.classTasksLoading,
  classTasksByDate: state.classCms.classTasksByDate,

  classStudentsLoading: state.classCms.classStudentsLoading,
  classStudents: state.classCms.classStudents
});


CMSClassPageContainer = connect(mapStateToProps, mapDispatchToProps)(CMSClassPageContainer);

export default CMSClassPageContainer;
