import React from 'react';
import { LoadingSpinner, PlaceholderText, ImageIcon } from 'components/atoms';
import { DateTasks } from 'components/molecules';


const StudentClassTasks = ({ classTasksLoading, classTasksByDate, toggleTaskCompletion }) => {
  let datesTasks = Object.entries(classTasksByDate).map(([date, tasks], index) => (
    <DateTasks
      key={`${date}_${index}`}
      date={date}
      tasks={tasks}
      showTitle={true}
      showClass={false}
      toggleTaskCompletion={toggleTaskCompletion}
    />
  ));

  return (
    <div className="page-content-block container">
      <hr className="splitter" />

      <div className="tasks">
        <h4 className="block-title text-right"><ImageIcon name="task" /> المهام / الواجبات</h4>

        <div className="tasks-container">
          <div className="row">
            {classTasksLoading ? (
              <LoadingSpinner large />
            ) : (
              datesTasks.length === 0 ? (
                <PlaceholderText text='لا يوجد مهام/واجباب في هذا الصف حتى الآن!' />
              ) : (
                datesTasks
              )
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default StudentClassTasks;
