import React from 'react';
import Page from '../Page/Page.js';
import { BackLink, StudentClassTasks, LoadingSpinner } from 'components';
import { routes } from 'router';
import { isEmpty } from "lodash";


const LMSClassPage = ({ class_, classTasksLoading, classTasksByDate, toggleTaskCompletion }) => {
  return (
    <Page id="lms-class-page">
      <div className="page-container">
        {isEmpty(class_) ? (
          <LoadingSpinner large />
        ) : (
          <>
            <h2 className="page-title text-center"><BackLink href={routes.studentDashboard} /> {class_.name} ({class_.subject})</h2>

            <div className="class-details-container">
              <div className="class-details">
                <div className="detail-point">
                  <i className="fas fa-school detail-point-icon"></i>
                  <label className="detail-point-label">المدرسة</label>
                  <p className="detail-point-value">{class_.school}</p>
                </div>

                <div className="detail-point">
                  <i className="fas fa-check-circle detail-point-icon"></i>
                  <label className="detail-point-label">المهام والواجبات</label>
                  <p className="detail-point-value">{class_['tasks_count']}</p>
                </div>
              </div>
            </div>

            <p className="text-center">هذه مفكرة مدرسية للصف الخامس - الشعبة ب، يمكنك من خلالها متابعة جدولك الدراسي خلال الأسبوع.</p>

            <StudentClassTasks
              classTasksLoading={classTasksLoading}
              classTasksByDate={classTasksByDate}
              toggleTaskCompletion={toggleTaskCompletion}
            />
          </>
        )}
      </div>
    </Page>
  );
}

export default LMSClassPage;
