import * as constants from './constants';


const INITIAL_STATE = {
  classesLoading: false,
  classes: [],

  joinClassLoading: false,
  joinClassError: ''
};


export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case constants.STUDENT_CLASSES_FETCH_START:
      return { ...state, classesLoading: true };

    case constants.STUDENT_CLASSES_FETCH_SUCCESS:
      return { ...state, classesLoading: false, classes: payload };

    case constants.STUDENT_CLASSES_FETCH_FAILURE:
      return { ...state, classesLoading: false };


    case constants.CLASS_JOIN_START:
      return { ...state, joinClassLoading: true };

    case constants.CLASS_JOIN_SUCCESS:
      return { ...state, joinClassLoading: false };

    case constants.CLASS_JOIN_FAILURE:
      return { ...state, joinClassLoading: false, joinClassError: payload };

    default:
      return state;
  }
}
