import React, { Component } from 'react';
import { reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { createValidator, required, minLength } from 'utils/validation';
import { login, showAlert, hideAlert } from 'store/actions';
import StudentLoginPage from 'pages/StudentLoginPage/StudentLoginPage';


class StudentLoginPageContainer extends Component {
  componentWillUnmount() {
    this.props.hideAlert();
  }

  componentDidUpdate(prevProps, prevState, snapshot){
    if (this.props.error)
      this.props.showAlert(this.props.error, 'danger');
  }

  render() {
    return (
      <StudentLoginPage {...this.props} />
    );
  }
}


const validate = createValidator({
  username: [required, minLength(2)],
  password: [required, minLength(5)]
});

const mapDispatchToProps = dispatch => ({
  showAlert: (message, type) => dispatch(showAlert(message, type)),
  hideAlert: () => dispatch(hideAlert()),
  onSubmit: (data, dispatch) => dispatch(login(data))
});

const mapStateToProps = state => ({
  initialValues: {},
  alert: state.alert
});


StudentLoginPageContainer = reduxForm({
  enableReinitialize: true,
  form: 'loginForm',
  destroyOnUnmount: false,
  validate,
})(StudentLoginPageContainer);

StudentLoginPageContainer = connect(mapStateToProps, mapDispatchToProps)(StudentLoginPageContainer);

export default StudentLoginPageContainer;
