import isEmail from 'validator/lib/isEmail'
import isInt from 'validator/lib/isInt'
// import isIn from 'validator/lib/isIn'
import isURL from 'validator/lib/isURL'


const isEmpty = value => value === undefined || value === null || value === '';
const join = rules => (value, data) =>
  rules.map(rule => rule(value, data)).filter(error => !!error)[0];

export const email = value => !isEmpty(value) && !isEmail(value) &&
  'البريد الإلكتروني غير صحيح';

export const url = value => !isEmpty(value) && !isURL(value) &&
  'الرابط غير صحيح';

export const required = value => isEmpty(value) &&
  'هذا الحقل مطلوب';

export const minLength = min => value => !isEmpty(value) && value.length < min &&
  `يجب أن لا يقل طول هذا الحقل عن ${min} حرف`;

export const maxLength = max => value => !isEmpty(value) && value.length > max &&
  `يجب أن لا يكون طول هذا الحقل أكثر من ${max} حرف`;

export const integer = value => !isInt(value) &&
  'يجب أن يكون هذا الحقل رقم';

export const firstAndLastName = value => !isEmpty(value) && value.split(' ').filter(s => s).length !== 2 &&
  'يجب أن يحتوي هذا الحقل على اسمك الأول واسم العائلة';

// export const oneOf = values => value => !isIn(value, values) &&
//   I18n.t('validations.oneOf', {oneOf: values.join(', ')})

export const match = field => (value, data) => data && value !== data[field] &&
  `يجب أن يطابق هذا الحقل حقل ${field}`;

// export const notIncluding = (thisField, otherField) => (value, data) => data && value && value.includes(data[otherField] ? data[otherField].toLowerCase() : data[otherField]) &&
//   I18n.t('validations.notIncluding', {
//     thisField: I18n.t(`labels.${thisField}`),
//     otherField: I18n.t(`labels.${otherField}`)
//   })

export const createValidator = rules => (data = {}) => {
  const errors = {};
  Object.keys(rules).forEach((key) => {
    const rule = join([].concat(rules[key]));
    const error = rule(data[key], data);
    if (error) {
      errors[key] = error;
    }
  });
  return errors;
};

