import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  fetchStudentClass,
  fetchStudentClassTasks,
  toggleClassTaskCompletion
} from 'store/actions';
import LMSClassPage from 'pages/LMSClassPage/LMSClassPage';


class LMSClassPageContainer extends Component {
  constructor(props) {
    super(props);

    this.classID = this.props.match.params.id;
  }

  componentDidMount() {
    this.props.fetchStudentClass(this.classID);
    this.props.fetchStudentClassTasks(this.classID);
  }

  render() {
    const { class_, classTasksByDate, classTasksLoading, toggleTaskCompletion } = this.props;

    return (
      <LMSClassPage
        class_={class_}
        classTasksLoading={classTasksLoading}
        classTasksByDate={classTasksByDate}
        toggleTaskCompletion={toggleTaskCompletion}
      />
    );
  }
}


const mapDispatchToProps = dispatch => ({
  fetchStudentClass: classID => dispatch(fetchStudentClass(classID)),
  fetchStudentClassTasks: classID => dispatch(fetchStudentClassTasks(classID)),
  toggleTaskCompletion: taskID => dispatch(toggleClassTaskCompletion(taskID))
});

const mapStateToProps = state => ({
  class_: state.classLms.class_,
  classTasksLoading: state.classLms.classTasksLoading,
  classTasksByDate: state.classLms.classTasksByDate
});


LMSClassPageContainer = connect(mapStateToProps, mapDispatchToProps)(LMSClassPageContainer);

export default LMSClassPageContainer;
