import React from 'react';
import { LoadingSpinner, PlaceholderText, ImageIcon } from 'components/atoms';
import { DateTasks } from 'components/molecules';


const TasksToday = ({ tasksTodayLoading, tasksToday, toggleTaskCompletion }) => {
  const todayDate = new Date().toLocaleDateString();

  return (
    <div className="page-content-block container">
      <div className="tasks-today">
        <h4 className="block-title text-right"><ImageIcon name="task" /> مهام / واجبات اليوم ({todayDate})</h4>

        <div className="tasks-container">
          <div className="row">
            {tasksTodayLoading ? (
              <LoadingSpinner large />
            ) : (
              tasksToday.length === 0 ? (
                <PlaceholderText text='لا يوجد مهام/واجباب لهذا اليوم!' />
              ) : (
                <DateTasks
                  tasks={tasksToday}
                  showTitle={false}
                  showClass
                  toggleTaskCompletion={toggleTaskCompletion}
                />
              )
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default TasksToday;
