import api from 'services/api';
import * as constants from './constants';
import { toast } from "react-toastify";


export const fetchStudentTasksToday = () => {
  return dispatch => {
    dispatch({ type: constants.STUDENT_TASKS_TODAY_FETCH_START });

    return api.fetchStudentTasksToday()
      .then(rsp => {
        dispatch({ type: constants.STUDENT_TASKS_TODAY_FETCH_SUCCESS, payload: rsp.data });
      })
      .catch(error => {
        dispatch({ type: constants.STUDENT_TASKS_TODAY_FETCH_FAILURE });
        toast('حدث خطأ في جلب مهام وواجبات اليوم، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};


export const toggleTaskCompletion = taskID => {
  return dispatch => {
    return api.toggleTaskCompletion(taskID)
      .then(rsp => {
        dispatch({ type: constants.STUDENT_TASK_COMPLETION_TOGGLE_SUCCESS, payload: rsp.data });

        if (rsp.data.completed)
          toast('تم انجاز المهمة بنجاح!', { type: toast.TYPE.SUCCESS });
        else
          toast('تم الغاء اتمامك للمهمة', { type: toast.TYPE.WARNING });
      })
      .catch(error => {
        toast('حدث خطأ في تحديث المهمة، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};
