import { SHOW_ALERT, HIDE_ALERT } from 'store/constants';


const DEFAULT_LEVEL = 'default';

const INITIAL_STATE = {
  visible: false,
  type: DEFAULT_LEVEL,
  message: '',
};


export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case SHOW_ALERT:
      return { ...state, visible: true, type: payload.type || DEFAULT_LEVEL, message: payload.message };

    case HIDE_ALERT:
      return { ...state, visible: false, message: '' };

    default:
      return state;
  }
}
