import * as constants from './constants';
import { find } from 'lodash';


const INITIAL_STATE = {
  classesLoading: false,
  classes: [],

  classSubmitted: false,

  editClass: {},
};


const getClassByID = (classes, classID) => find(classes, ['id', classID]);

const updateClass = (classes, class_) => {
  let _classes = [...classes];
  let classIndex = _classes.findIndex((obj => obj.id === class_.id));
  _classes[classIndex] = class_;
  return _classes;
};


export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case constants.TEACHER_CLASSES_FETCH_START:
      return { ...state, classesLoading: true };

    case constants.TEACHER_CLASSES_FETCH_SUCCESS:
      return { ...state, classesLoading: false, classes: payload };

    case constants.TEACHER_CLASSES_FETCH_FAILURE:
      return { ...state, classesLoading: false };


    case constants.CLASS_CREATE_SUCCESS:
      return { ...state, classSubmitted: true, classes: [ ...state.classes, payload ] };

    case constants.CLASS_UPDATE_SUCCESS:
      return { ...state, classSubmitted: true, classes: updateClass(state.classes, payload) };


    case constants.CLASS_EDIT_SET:
      return { ...state, editClass: getClassByID(state.classes, payload) };

    case constants.CLASS_EDIT_UNSET:
      return { ...state, editClass: {} };

    default:
      return state;
  }
}
