import React from 'react';
import RouterLink from '../../atoms/RouterLink/RouterLink';
import { getUserHomePageRoute } from 'utils/helpers';
import session from 'services/session';
import { redirectToLogin } from 'utils/redirects';


const logoImage = require('assets/img/logo.png');

const onLogoutClick = () => session.logout();


const AppHeader = () => {
  return (
    <header className="site-header">
      <div className="container">
        <div className="row pt-3 pb-3">
          <div className="col-lg-10 col-12 d-flex logo-container">
            <RouterLink href={getUserHomePageRoute()}>
              <img className="logo" alt="mofakerati" src={logoImage} />
            </RouterLink>
            <h2 className="slogan">مفكرة</h2>
          </div>

          {session.isAuthenticated() ? (
            <div className="header-profile-container">
              <div className="dropdown">
                <button
                  className="btn btn-link dropdown-toggle"
                  type="button"
                  id="profileDropdownMenuButton"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false">
                  أهلاً {session.currentUser().firstName}
                </button>

                <div className="dropdown-menu" aria-labelledby="profileDropdownMenuButton">
                  <button type="button" className="dropdown-item btn btn-link link btn-logout" onClick={onLogoutClick}>خروج</button>
                </div>
              </div>
            </div>
          ) : (
            <div className="header-profile-container">
              <button className="btn btn-primary" onClick={redirectToLogin}>
                دخول
              </button>
            </div>
          )}
        </div>
      </div>
    </header>
  );
}


export default AppHeader;
