import React from 'react';
import { nav } from 'router';


const BackLink = ({ history, href, onClick }) => {
  const handleOnClick = () => {
    if (onClick)
      return onClick();

    if (href)
      return nav(href);

    if (history)
      return history.goBack();
  }

  return (
    <span className="back-link" onClick={handleOnClick}>
      <i className="fas fa-arrow-right"></i>
    </span>
  );
};

export default BackLink;
