import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchStudentTasksToday, toggleTaskCompletion } from 'store/actions';
import TasksToday from 'components/organisms/TasksToday/TasksToday';


class TasksTodayContainer extends Component {
  componentDidMount() {
    this.props.fetchStudentTasksToday();
  }

  render() {
    const { tasksTodayLoading, tasksToday, toggleTaskCompletion } = this.props;

    return (
      <TasksToday
        tasksTodayLoading={tasksTodayLoading}
        tasksToday={tasksToday}
        toggleTaskCompletion={toggleTaskCompletion}
      />
    );
  }
}


const mapDispatchToProps = dispatch => ({
  fetchStudentTasksToday: () => dispatch(fetchStudentTasksToday()),
  toggleTaskCompletion: taskID => dispatch(toggleTaskCompletion(taskID))
});

const mapStateToProps = state => ({
  tasksTodayLoading: state.studentTasks.tasksTodayLoading,
  tasksToday: state.studentTasks.tasksToday
});


TasksTodayContainer = connect(mapStateToProps, mapDispatchToProps)(TasksTodayContainer);

export default TasksTodayContainer;
