import React from 'react';
import { nav, routes } from 'router';


const StudentClassCard = ({ class_ }) => {
  const { id, name, subject, school } = class_;

  const onButtonClick = () => nav(routes.classLMS.replace(':id', id));

  return (
    <div className="card class-card">
      <div className="card-body">
        <h5 className="card-title class-name">{name}</h5>
        <p className="card-text">المادة: <span className="card-text-bold">{subject}</span></p>
        <p className="card-text">المدرسة: <span className="card-text-bold">{school}</span></p>

        <button className="btn btn-primary btn-class-details" onClick={onButtonClick}>مفكرة الصف</button>
      </div>
    </div>
  );
};

export default StudentClassCard;
