import React, { Component } from 'react';
import { reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { createValidator, required, minLength } from 'utils/validation';
import { login, showAlert, hideAlert } from 'store/actions';
import TeacherLoginPage from 'pages/TeacherLoginPage/TeacherLoginPage';


class TeacherLoginPageContainer extends Component {
  componentWillUnmount() {
    this.props.hideAlert();
  }

  componentDidUpdate(prevProps, prevState, snapshot){
    if (this.props.error)
      this.props.showAlert(this.props.error, 'danger');
  }

  render() {
    return (
      <TeacherLoginPage {...this.props} />
    );
  }
}


const validate = createValidator({
  username: [required, minLength(2)],
  password: [required, minLength(5)]
});

const mapDispatchToProps = dispatch => ({
  showAlert: (message, type) => dispatch(showAlert(message, type)),
  hideAlert: () => dispatch(hideAlert()),
  onSubmit: (data, dispatch) => dispatch(login(data))
});

const mapStateToProps = state => ({
  initialValues: {},
  alert: state.alert
});


TeacherLoginPageContainer = reduxForm({
  enableReinitialize: true,
  form: 'loginForm',
  destroyOnUnmount: false,
  validate,
})(TeacherLoginPageContainer);

TeacherLoginPageContainer = connect(mapStateToProps, mapDispatchToProps)(TeacherLoginPageContainer);

export default TeacherLoginPageContainer;
