import { nav, routes } from 'router';
import session from 'services/session';
import * as _ from 'lodash';


export const isDev = () => process.env.NODE_ENV !== "production";

export const isBrowser = () => typeof window !== "undefined";

export const parseErrorResponse = (rsp) => {
  let rspData = rsp.data;
  let errorMessage = 'حدث خطأ غير متوقع، يرجى المحاولة فى وقت لاحق';

  if (rspData && 'message' in rspData)
    errorMessage = rspData.message;

  return { _error: errorMessage };
};


export const redirectAfterAuth = () => {
  let route = session.currentUser().userType === 'student' ? routes.studentDashboard : routes.teacherDashboard;
  nav(route);
};


export const getUserHomePageRoute = () => {
  let currentUser = session.currentUser();

  if (!currentUser)
    return routes.home;
  else if (currentUser.userType === 'teacher')
    return routes.teacherDashboard;
  else if (currentUser.userType === 'student')
    return routes.studentDashboard;

  return routes.home;
}


export const groupTasksByDate = tasks => {
  return _.groupBy(tasks, (task) => task.date);
}


export const getUserFullName = user => {
  return `${user['first_name'] || user['firstName']} ${user['last_name'] || user['lastName']}`;
}


export const getDateString = date => {
  if (typeof date === 'object')
    return date.toISOString().slice(0, 10);
  else if (date.length === 10)
    return date;
  else
    return date.slice(0, 10);
};
