import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Modal from 'react-modal';
import { isMobile } from "react-device-detect";
import FormAlert from 'components/molecules/FormAlert/FormAlert';


const modalStyle = {
  overlay: {
    backgroundColor: 'rgba(0, 0, 0, 0.4)'
  },
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)',
    padding               : 0,
    width                 : isMobile ? '90%' : '500px',
  }
};


class JoinClassModal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      code: ''
    }
  }

  onSubmit = (e) => {
    e.preventDefault();

    const { joinClass, closeModal } = this.props;
    const { code } = this.state;

    joinClass(code, { callback: closeModal });
  };

  onCodeChange = event => {
    this.setState({ ...this.state, code: event.target.value });
  }

  render () {
    const { isOpen, closeModal, joinClassLoading, joinClassError } = this.props;

    return (
      <Modal
        isOpen={isOpen}
        onRequestClose={closeModal}
        style={modalStyle}
        appElement={document.getElementById('root')}
      >
        <div className="add-class-modal">
          <div className="modal-header">
            <h5 className="modal-title">انضم إلى صف جديد</h5>
            <button type="button" className="close" aria-label="Close" onClick={closeModal}>
              <span aria-hidden="true">&times;</span>
            </button>
          </div>

          <div className="modal-body">
            <FormAlert
              visible={!joinClassLoading && !!joinClassError}
              type="danger"
              message={joinClassError}
              isFullWidth
            />

            <form className="form registration-form" onSubmit={this.onSubmit}>
              <div className="form-group">
                <label htmlFor="class-name">رمز الصف/المفكرة</label>
                <input
                  type="text"
                  className="form-control form-control-lg"
                  id="class-name"
                  placeholder="مثال: 1GDG3"
                  onChange={this.onCodeChange}
                />
              </div>

              <button type="submit" className="btn btn-primary btn-lg btn-block" disabled={joinClassLoading}>انضم</button>
            </form>
          </div>
        </div>
      </Modal>
    );
  }
}

PropTypes.propTypes = {
  isOpen: PropTypes.bool,
  closeModal: PropTypes.func,
  joinClass: PropTypes.func,
  joinClassLoading: PropTypes.bool,
  joinClassError: PropTypes.string
};

export default JoinClassModal;
