import React, { Component } from 'react';
import Modal from 'react-modal';
import { isMobile } from 'react-device-detect';
import { reduxForm } from 'redux-form';
import { Form, Field } from 'redux-form';
import { connect } from 'react-redux';
import { createValidator, required, minLength } from 'utils/validation';
import { submitClassTask, hideAlert, showAlert } from 'store/actions';
import FormRichTextField from 'components/molecules/FormRichTextField/FormRichTextField';
import FormDatetimeField from 'components/molecules/FormDatetimeField/FormDatetimeField';
import FormAlert from 'components/molecules/FormAlert/FormAlert';
import {isEmpty} from "lodash";


const modalStyle = {
  overlay: {
    backgroundColor: 'rgba(0, 0, 0, 0.4)'
  },
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)',
    padding               : 0,
    width                 : isMobile ? '90%' : '650px',
  }
};


class TaskModal extends Component {

  componentWillUnmount() {
    this.props.hideAlert();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (this.props.error)
      this.props.showAlert(this.props.error, 'danger');

    if (this.props.submitSucceeded)
      this.props.closeModal();
  }

  render() {
    const { handleSubmit, submitting, alert, isOpen, isEditMode, closeModal } = this.props;

    let modalTitle = isEditMode ? 'عدّل المهمة/الواجب' : 'أضف مهمة/واجب جديد';
    let submitButtonText = isEditMode ? 'عدّل المهمة/الواجب' : 'أضف المهمة/الواجب';

    return (
      <Modal
        isOpen={isOpen}
        onRequestClose={closeModal}
        style={modalStyle}
        appElement={document.getElementById('root')}
      >
        <div className="add-class-modal">
          <div className="modal-header">
            <h5 className="modal-title">{modalTitle}</h5>
            <button type="button" className="close" aria-label="Close" onClick={closeModal}>
              <span aria-hidden="true">&times;</span>
            </button>
          </div>

          <div className="modal-body">
            <FormAlert visible={alert.visible} type={alert.type} message={alert.message}/>

            <Form className="form registration-form" onSubmit={handleSubmit}>
              <Field
                component={FormRichTextField}
                label="المهمة/الواجب"
                name="text"
                id="text"
                placeholder="مثال: دراسة الوحدة الثانية من مادة اللغة الإنجليزية كاملة مع حل جميع أسئلة الوحدة تعديل"
              />

              <Field
                component={FormDatetimeField}
                label="تاريخ ظهور المهمة/الواجب للطلاب"
                name="date"
                id="date"
                timeFormat={false}
              />

              <button type="submit" className="btn btn-primary btn-lg btn-block" disabled={submitting}>
                {submitButtonText}
              </button>
            </Form>
          </div>
        </div>
      </Modal>
    );
  }
};

const validate = createValidator({
  text: [required, minLength(4)],
  date: [required],
});

const mapDispatchToProps = dispatch => ({
  showAlert: (message, type) => dispatch(showAlert(message, type)),
  hideAlert: () => dispatch(hideAlert()),
  onSubmit: (data, dispatch, props) => dispatch(submitClassTask(props.classID, data))
});


const todayDate = new Date();

const mapStateToProps = state => {
  let editClassTask = state.classCms.editClassTask;
  let isEditMode = false;

  let initialValues = { date: todayDate };

  if (!isEmpty(editClassTask)) {
    initialValues = editClassTask;
    initialValues.date = new Date(initialValues.date);
    isEditMode = true;
  }

  return {
    initialValues,
    isEditMode,
    alert: state.alert
  };
};


TaskModal = reduxForm({
  enableReinitialize: true,
  form: 'taskForm',
  destroyOnUnmount: false,
  validate,
})(TaskModal);

TaskModal = connect(mapStateToProps, mapDispatchToProps)(TaskModal);


export default TaskModal;
