import axios from 'axios';
import session from 'services/session';
import { nav, routes } from 'router';


class APIBase {
  // baseURL = 'http://localhost:8000/api/';
  baseURL = 'https://mofkra-staging.herokuapp.com/api/';

  constructor() {
    axios.interceptors.response.use(response => {
      // Any status code that lie within the range of 2xx cause this function to trigger
      // Do something with response data
      return response;
    }, function (error) {
      // Any status codes that falls outside the range of 2xx cause this function to trigger
      // Do something with response error
      if (error.response && [401, 403].includes(error.response.status))
        session.logout();
      return Promise.reject(error);
    });
  }

  _handleIfNotAuthenticated = () => {
    if (!session.isAuthenticated())
      nav(routes.home);
  };

  _getAuthorizationConfigs = (configs = { headers: {} }) => {
    this._handleIfNotAuthenticated();

    let currentUser = session.currentUser();
    configs = {
      ...configs,
      headers: {
        ...configs.headers,
        Authorization: `JWT ${currentUser.token}`
      }
    };
    return configs;
  };

  _get = (url, authenticate = false, configs = { params: {}, headers: {} }) => {
    let _url = this.baseURL + url;

    if (authenticate)
      configs = this._getAuthorizationConfigs(configs);

    return axios.get(_url, configs);
  };

  _post = (url, authenticate = false, data = {}, configs = {}) => {
    let _url = this.baseURL + url;

    if (authenticate)
      configs = this._getAuthorizationConfigs(configs);

    return axios.post(_url, data, configs);
  };

  _put = (url, authenticate = false, data = {}, configs = {}) => {
    let _url = this.baseURL + url;

    if (authenticate)
      configs = this._getAuthorizationConfigs(configs);

    return axios.put(_url, data, configs);
  };

  _delete = (url, authenticate = false, configs = {}) => {
    let _url = this.baseURL + url;

    if (authenticate)
      configs = this._getAuthorizationConfigs(configs);

    return axios.delete(_url, configs);
  };
}


class API extends APIBase {
  login = data => {
    return this._post('auth/login/', false, data);
  };

  // Teacher APIs

  registerTeacher = data => {
    return this._post('auth/register/teacher/', false, data);
  };

  fetchTeacherClasses = () => {
    return this._get('teacher/classes/', true);
  };

  fetchTeacherClass = (classID) => {
    return this._get(`teacher/classes/${classID}/`, true);
  };

  fetchTeacherClassTasks = classID => {
    return this._get(`teacher/classes/${classID}/tasks/`, true);
  };

  fetchTeacherClassTask = (classID, taskID) => {
    return this._get(`teacher/classes/${classID}/tasks/${taskID}/`, true);
  };

  fetchTeacherClassStudents = classID => {
    return this._get(`teacher/classes/${classID}/students/`, true);
  };

  createClass = data => {
    return this._post('teacher/classes/', true, data);
  }

  updateClass = (classID, data) => {
    return this._put(`teacher/classes/${classID}/`, true, data);
  }

  createClassTask = (classID, data) => {
    return this._post(`teacher/classes/${classID}/tasks/`, true, data);
  }

  updateClassTask = (classID, taskID, data) => {
    return this._put(`teacher/classes/${classID}/tasks/${taskID}/`, true, data);
  }

  deleteClass = classID => {
    return this._delete(`teacher/classes/${classID}/`, true);
  }

  deleteClassTask = (classID, taskID) => {
    return this._delete(`teacher/classes/${classID}/tasks/${taskID}/`, true);
  }

  // Student APIs

  registerStudent = data => {
    return this._post('auth/register/student/', false, data);
  };

  fetchStudentClasses = () => {
    return this._get('student/classes/', true);
  };

  fetchStudentClass = classID => {
    return this._get(`student/classes/${classID}/`, true);
  };

  fetchStudentClassTasks = classID => {
    return this._get(`student/classes/${classID}/tasks/`, true);
  };

  fetchStudentTasksToday = () => {
    return this._get('student/tasks_today/', true);
  };

  joinClass = code => {
    return this._post('student/enroll/', true, { code });
  };

  toggleTaskCompletion = taskID => {
    return this._post('student/states/toggle_task_completion/', true, { task_id: taskID });
  };
}


export default new API();
