import api from 'services/api';
import * as constants from './constants';
import { groupTasksByDate } from 'utils/helpers';
import { SubmissionError } from 'redux-form';
import { isEmpty } from 'lodash';
import { toast } from "react-toastify";


export const fetchTeacherClass = classID => {
  return dispatch => {
    return api.fetchTeacherClass(classID)
      .then(rsp => {
        dispatch({ type: constants.TEACHER_CLASS_FETCH_SUCCESS, payload: rsp.data });
      })
      .catch(error => {
        toast('حدث خطأ في جلب الصف، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};


export const fetchTeacherClassTasks = classID => {
  return dispatch => {
    dispatch({ type: constants.TEACHER_CLASS_TASKS_FETCH_START });

    return api.fetchTeacherClassTasks(classID)
      .then(rsp => {
        let groupedTasks = groupTasksByDate(rsp.data);
        dispatch({ type: constants.TEACHER_CLASS_TASKS_FETCH_SUCCESS, payload: groupedTasks });
      })
      .catch(error => {
        dispatch({ type: constants.TEACHER_CLASS_TASKS_FETCH_FAILURE });
        toast('حدث خطأ في جلب مهام الصف، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};


export const fetchTeacherClassStudents = classID => {
  return dispatch => {
    dispatch({ type: constants.TEACHER_CLASS_STUDENTS_FETCH_START });

    return api.fetchTeacherClassStudents(classID)
      .then(rsp => {
        dispatch({ type: constants.TEACHER_CLASS_STUDENTS_FETCH_SUCCESS, payload: rsp.data });
      })
      .catch(error => {
        dispatch({ type: constants.TEACHER_CLASS_STUDENTS_FETCH_FAILURE });
        toast('حدث خطأ في جلب طلاب الصف، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};


export const submitClassTask = (classID, data) => {
  return (dispatch, getState) => {
    let state = getState();
    let editClassTask = state.classCms.editClassTask;

    if (isEmpty(editClassTask)) {
      dispatch(createClassTask(classID, data));
      return;
    }

    dispatch(updateClassTask(classID, editClassTask.id, data));
  }
};


export const createClassTask = (classID, data) => {
  data.date = data.date.toISOString().slice(0, 10);

  return dispatch => {
    return api.createClassTask(classID, data)
      .then(rsp => {
        dispatch({ type: constants.CLASS_TASK_CREATE_SUCCESS });
        dispatch(fetchTeacherClassTasks(classID));
        toast('تم اضافة المهمة بنجاح', { type: toast.TYPE.SUCCESS });
      })
      .catch(error => {
        throw new SubmissionError(error.response ? error.response.data : {
          '_error': 'فشل في اضافة مهمة/واجب جديد، الرجاء المحاولة لاحقاً'
        });
      });
  };
};


export const updateClassTask = (classID, taskID, data) => {
  data.date = data.date.toISOString().slice(0, 10);

  return dispatch => {
    return api.updateClassTask(classID, taskID, data)
      .then(rsp => {
        dispatch({ type: constants.CLASS_TASK_UPDATE_SUCCESS, payload: rsp.data });
        dispatch(fetchTeacherClassTasks(classID));
        toast('تم تعديل المهمة بنجاح', { type: toast.TYPE.SUCCESS });
      })
      .catch(error => {
        throw new SubmissionError(error.response ? error.response.data : {
          '_error': 'فشل في تعديل المهمة/الواجب، الرجاء المحاولة لاحقاً'
        });
      });
  };
};


export const deleteClassTask = (classID, taskID) => {
  return dispatch => {
    return api.deleteClassTask(classID, taskID)
      .then(rsp => {
        dispatch(fetchTeacherClassTasks(classID));
        toast('تم حذف المهمة بنجاح', { type: toast.TYPE.SUCCESS });
      })
      .catch(error => {
        toast('حدث خطأ في حذف المهمة، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};


export const setEditClassTask = taskID => {
  return dispatch => {
    dispatch({ type: constants.CLASS_TASK_EDIT_SET, payload: taskID });
  };
};


export const unsetEditClassTask = () => {
  return dispatch => {
    dispatch({ type: constants.CLASS_TASK_EDIT_UNSET });
  };
};
