import React from 'react';
import TeacherClassCard from 'components/molecules/ClassCard/TeacherClassCard';
import ClassModal from 'components/molecules/ClassModal/ClassModal';
import { LoadingSpinner, PlaceholderText, ImageIcon } from 'components/atoms';


const TeacherClasses = ({ classesLoading, classes, deleteClass, isClassModalOpen,
                          openClassModal, openEditClassModal, closeClassModal }) => {

  return (
    <div className="page-content-block container">
      {isClassModalOpen ? (
        <ClassModal
          isOpen={isClassModalOpen}
          closeModal={closeClassModal}
        />
      ) : null}

      <div className="classes">
        <h4 className="block-title text-right"><ImageIcon name="class" />صفوفي
          <button
            type="button"
            className="btn btn-outline-primary btn-add-class"
            onClick={openClassModal}>
            أضف صف جديد
          </button>
        </h4>

        <div className="classes-container">
          <div className="row">
            {classesLoading ? (
              <LoadingSpinner large />
            ) : (
              classes.length === 0 ? (
                <PlaceholderText text='لا يوجد لديك صفوف!' />
              ) : (
                classes.map((class_, index) => (
                  <div className="col-sm-6" key={`${class_.id}_${index}`}>
                    <TeacherClassCard
                      class_={class_}
                      deleteClass={deleteClass}
                      openEditClassModal={openEditClassModal}
                    />
                  </div>
                ))
              )
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default TeacherClasses;
