import React from 'react';
import Page from '../Page/Page.js';
import { isEmpty } from 'lodash';
import { routes } from 'router';
import {
  BlockSeparator,
  Link,
  TaskModal,
  BackLink,
  TeacherClassTasks,
  TeacherClassStudents,
  LoadingSpinner
} from 'components';


const CMSClassPage = ({ class_, classTasksLoading, classTasksByDate, activeTab, changeActiveTab,
                        isTaskModalOpen, openTaskModal, openEditTaskModal, closeTaskModal,
                        classStudentsLoading, classStudents, deleteClassTask}) => {

  const selectTasksTab = () => changeActiveTab('tasks');
  const selectStudentsTab = () => changeActiveTab('students');

  return (
    <Page id="cms-class-page">
      <div className="page-container">
        {isEmpty(class_) ? (
          <LoadingSpinner large />
        ) : (
          <>
            <h2 className="page-title text-center"><BackLink href={routes.teacherDashboard} />{class_.name} <span className="badge badge-secondary" data-toggle="tooltip" data-placement="top" title="رمز الصف/المفكرة">{class_.code}</span></h2>

            <div className="class-details-container">
              <div className="class-details">
                <div className="detail-point">
                  <i className="fas fa-book detail-point-icon"></i>
                  <label className="detail-point-label">المادة</label>
                  <p className="detail-point-value">{class_.subject}</p>
                </div>

                <div className="detail-point">
                  <i className="fas fa-school detail-point-icon"></i>
                  <label className="detail-point-label">المدرسة</label>
                  <p className="detail-point-value">{class_.school}</p>
                </div>

                <div className="detail-point">
                  <i className="fas fa-users detail-point-icon"></i>
                  <label className="detail-point-label">الطلاب المنضمين</label>
                  <p className="detail-point-value">{class_['students_count']}</p>
                </div>

                <div className="detail-point">
                  <i className="fas fa-check-circle detail-point-icon"></i>
                  <label className="detail-point-label">المهام والواجبات</label>
                  <p className="detail-point-value">{class_['tasks_count']}</p>
                </div>
              </div>
            </div>

            <p className="text-center">هذه مفكرة مدرسية للصف الخامس - الشعبة ب، يمكنك من خلالها تنظيم جدول طلابك خلال الأسبوع ومساعدتهم في إنجاز دراستهم بنجاح.</p>

            <ul className="nav nav-pills">
              <li className="nav-item">
                <Link className={`nav-link ${activeTab === 'tasks' ? 'active' : ''}`} underline={false} onClick={selectTasksTab}>المهام / الواجبات</Link>
              </li>
              <li className="nav-item">
                <Link className={`nav-link ${activeTab === 'students' ? 'active' : ''}`} underline={false} onClick={selectStudentsTab}>الطلاب</Link>
              </li>
            </ul>

            <BlockSeparator />

            {activeTab === 'tasks' ? (
              <>
                {isTaskModalOpen ? (
                  <TaskModal
                    isOpen={isTaskModalOpen}
                    closeModal={closeTaskModal}
                    classID={class_.id}
                  />
                ) : null}


                <TeacherClassTasks
                  classTasksLoading={classTasksLoading}
                  classTasksByDate={classTasksByDate}
                  openTaskModal={openTaskModal}
                  openEditTaskModal={openEditTaskModal}
                  deleteClassTask={deleteClassTask}
                />
              </>
            ) : null}

            {activeTab === 'students' ? (
              <TeacherClassStudents
                classStudentsLoading={classStudentsLoading}
                classStudents={classStudents}
              />
            ) : null}

          </>
        )}
      </div>
    </Page>
  );
}

export default CMSClassPage;
