import React, { useEffect } from 'react';
import appHistory from 'appHistory';
import { Router, Route, Switch, useLocation } from 'react-router-dom';
import { withAuth, withGuest } from 'utils';
import routes from './routes';
import {
  HomePage,
  TeacherDashboardPage,
  StudentDashboardPage
} from 'pages';
import {
  TeacherRegistrationPageContainer,
  StudentRegistrationPageContainer,

  TeacherLoginPageContainer,
  StudentLoginPageContainer,

  LMSClassPageContainer,
  CMSClassPageContainer
} from 'containers';


const ScrollToTop = ({ children }) => {
  // This is needed because when navigating using
  // hash router it doesn't scroll to top of the page
  const { pathname } = useLocation();
  useEffect(() => window.scrollTo(0, 0), [pathname]);
  return children;
};


const AppRouter = () => {
  return (
    <Router history={appHistory}>
      <ScrollToTop>
        <Switch>
          <Route path={routes.home} component={withGuest(HomePage)} exact />

          <Route path={routes.teacherRegistration} component={withGuest(TeacherRegistrationPageContainer)} />
          <Route path={routes.studentRegistration} component={withGuest(StudentRegistrationPageContainer)} />

          <Route path={routes.teacherLogin} component={withGuest(TeacherLoginPageContainer)} />
          <Route path={routes.studentLogin} component={withGuest(StudentLoginPageContainer)} />

          <Route path={routes.teacherDashboard} component={withAuth(TeacherDashboardPage)} />
          <Route path={routes.studentDashboard} component={withAuth(StudentDashboardPage)} />

          <Route path={routes.classCMS} component={withAuth(CMSClassPageContainer)} />
          <Route path={routes.classLMS} component={withAuth(LMSClassPageContainer)} />
        </Switch>
      </ScrollToTop>
    </Router>
  )
};

export default AppRouter;
