import api from 'services/api';
import { isEmpty } from 'lodash';
import * as constants from './constants';
import { SubmissionError } from "redux-form";
import { toast } from 'react-toastify';


export const fetchTeacherClasses = () => {
  return dispatch => {
    dispatch({ type: constants.TEACHER_CLASSES_FETCH_START });

    return api.fetchTeacherClasses()
      .then(rsp => {
        dispatch({ type: constants.TEACHER_CLASSES_FETCH_SUCCESS, payload: rsp.data });
      })
      .catch(error => {
        dispatch({ type: constants.TEACHER_CLASSES_FETCH_FAILURE });
        toast('حدث خطأ في جلب الصفوف، حاول مرة أخرى', { type: toast.TYPE.ERROR });
      });
  };
};


export const submitClass = data => {
  return (dispatch, getState) => {
    let state = getState();
    let editClass = state.teacherClasses.editClass;

    if (isEmpty(editClass)) {
      dispatch(createClass(data));
      return;
    }

    dispatch(updateClass(editClass.id, data));
  }
};


export const createClass = data => {
  return dispatch => {
    data.name = data.name.trim();
    data.subject = data.subject.trim();
    data.school = data.school.trim();

    return api.createClass(data)
      .then(rsp => {
        dispatch({ type: constants.CLASS_CREATE_SUCCESS, payload: rsp.data });
        toast('تم إضافة الصف بنجاح', { type: toast.TYPE.SUCCESS });
      })
      .catch(error => {
        throw new SubmissionError(error.response ? error.response.data : {
          '_error': 'حدث خطأ في اضافة صف جديد، حاول مرة أخرى'
        });
      });
  };
};


export const updateClass = (classID, data) => {
  return dispatch => {
    data.name = data.name.trim();
    data.subject = data.subject.trim();
    data.school = data.school.trim();

    return api.updateClass(classID, data)
      .then(rsp => {
        dispatch({ type: constants.CLASS_UPDATE_SUCCESS, payload: rsp.data });
        toast('تم تعديل الصف بنجاح', { type: toast.TYPE.SUCCESS });
      })
      .catch(error => {
        throw new SubmissionError(error.response ? error.response.data : {
          '_error': 'حدث خطأ في تعديل الصف، حاول مرة أخرى'
        });
      });
  };
};


export const deleteClass = classID => {
  return dispatch => {
    return api.deleteClass(classID)
      .then(rsp => {
        dispatch(fetchTeacherClasses());
        toast('تم حذف الصف بنجاح', { type: toast.TYPE.SUCCESS });
      })
      .catch(error => {
        toast('حدث خطأ في حذف الصف، حاول مرة أخرى', { type: toast.TYPE.ERROR });
      });
  };
};


export const setEditClass = classID => {
  return dispatch => {
    dispatch({ type: constants.CLASS_EDIT_SET, payload: classID });
  };
};


export const unsetEditClass = () => {
  return dispatch => {
    dispatch({ type: constants.CLASS_EDIT_UNSET });
  };
};
