import React from 'react';
import PropTypes from 'prop-types';


const FormRichTextField = ({ label, id, helpText, placeholder, isFullWidth, className,
                         rows, meta, autoFocus, disabled, input, children }) => {
  let errorExists = meta && ( meta.touched || meta.active ) && meta.error;
  let isValueValid = meta && (meta.touched || meta.active) && !meta.error;

  return (
    <div className={`form-group form-text-field ${ errorExists ? 'has-error' : ''} ${isFullWidth ? 'full-width' : ''}`}>
      {label ? (
        <label className="control-label" htmlFor={id}>{label}</label>
      ) : null }

      {helpText ? (
        <small className="form-text text-muted">{helpText}</small>
      ) : null }

      <textarea
        className={`form-control ${errorExists ? 'is-invalid' : ''} ${isValueValid ? 'is-valid' : ''} ${className ? className : ''}`}
        id={id}
        placeholder={placeholder}
        disabled={disabled}
        autoFocus={autoFocus}
        rows={rows}
        {...input}
      />

      {children}

      { errorExists ? (
        <span id={`${id}-error`} className="invalid-feedback">{meta.error}</span>
      ) : null}
    </div>
  )
};


FormRichTextField.propTypes = {
  label: PropTypes.string,
  id: PropTypes.string.isRequired,
  helpText: PropTypes.string,
  placeholder: PropTypes.string,
  isFullWidth: PropTypes.bool,
  meta: PropTypes.shape({
    touched: PropTypes.bool,
    error: PropTypes.string
  }),
  input: PropTypes.shape({
    name: PropTypes.string.isRequired,
    value: PropTypes.string
  }),
  autoFocus: PropTypes.bool,
  disabled: PropTypes.bool,
  rows: PropTypes.number,
};


FormRichTextField.defaultProps = {
  isFullWidth: true,
  hasError: false,
  rows: 5,
};


export default FormRichTextField;
