import * as constants from './constants';


const INITIAL_STATE = {
  tasksTodayLoading: false,
  tasksToday: []
};


const updateTaskCompletion = (tasks, task) => {
  let _tasks = [...tasks];
  let taskIndex = _tasks.findIndex((obj => obj.id === task.id));
  _tasks[taskIndex] = task;
  return _tasks;
};


export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case constants.STUDENT_TASKS_TODAY_FETCH_START:
      return { ...state, tasksTodayLoading: true };

    case constants.STUDENT_TASKS_TODAY_FETCH_SUCCESS:
      return { ...state, tasksTodayLoading: false, tasksToday: payload };

    case constants.STUDENT_TASKS_TODAY_FETCH_FAILURE:
      return { ...state, tasksTodayLoading: false };


    case constants.STUDENT_TASK_COMPLETION_TOGGLE_SUCCESS:
      return { ...state, tasksToday: updateTaskCompletion(state.tasksToday, payload) };

    default:
      return state;
  }
}
