import React from 'react';
import { nav, routes } from 'router';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import 'components/ConfirmAlert/ConfirmAlert.css';


const TeacherClassCard = ({ class_, deleteClass, openEditClassModal }) => {
  const { id, name, subject, school, code, students_count } = class_;

  const onClassEdit = () => openEditClassModal(id);
  const onButtonClick = () => nav(routes.classCMS.replace(':id', id));

  const onClassDelete = () => {
    confirmAlert({
      title: 'حذف الصف',
      message: `هل أنت متأكد من أنك تريد حذف هذا الصف؟ (${name})`,
      buttons: [
        {
          label: 'لا',
          onClick: () => {}
        },
        {
          label: 'نعم',
          onClick: () => deleteClass(id)
        }
      ]
    });
  };

  return (
    <div className="card class-card">
      <div className="card-body">
        <h5 className="card-title class-name">{name}</h5>

        <div className="btn-group btn-dropdown-options">
          <button type="button" className="btn btn-outline-secondary btn-sm dropdown-toggle" data-toggle="dropdown" aria-haspopup="true"
                  aria-expanded="false" />
          <div className="dropdown-menu">
            <span className="link dropdown-item" onClick={onClassEdit}>تعديل</span>
            <span className="link dropdown-item" onClick={onClassDelete}>حذف</span>
          </div>
        </div>

        <span>
          <p className="card-text">المادة: {subject}</p>
          <p className="card-text">المدرسة: {school}</p>
          <p className="card-text">عدد الطلاب: {students_count}</p>
          <p className="card-text">رمز الصف/المفكرة:
            &nbsp;<span className="badge badge-secondary code-badge">{code}</span>
          </p>
        </span>

        <button className="btn btn-primary btn-class-details" onClick={onButtonClick}>تفاصيل الصف</button>
      </div>
    </div>
  );
};

export default TeacherClassCard;
