import React, { Component } from 'react';
import { reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { createValidator, required, email, minLength, match, firstAndLastName } from 'utils/validation';
import { registerTeacher, showAlert, hideAlert } from 'store/actions';
import TeacherRegistrationPage from 'pages/TeacherRegistrationPage/TeacherRegistrationPage';


class TeacherRegistrationPageContainer extends Component {
  componentWillUnmount() {
    this.props.hideAlert();
  }

  componentDidUpdate(prevProps, prevState, snapshot){
    if (this.props.error)
      this.props.showAlert(this.props.error, 'danger');
  }

  render() {
    return (
      <TeacherRegistrationPage {...this.props} />
    );
  }
}


const validate = createValidator({
  email: [required, email],
  username: [required, minLength(2)],
  name: [required, minLength(2), firstAndLastName],
  password: [required, minLength(5)],
  password_confirmation: [required, match('password')]
});

const mapDispatchToProps = dispatch => ({
  showAlert: (message, type) => dispatch(showAlert(message, type)),
  hideAlert: () => dispatch(hideAlert()),
  onSubmit: (data, dispatch) => dispatch(registerTeacher(data))
});

const mapStateToProps = state => ({
  initialValues: {},
  alert: state.alert
});


TeacherRegistrationPageContainer = reduxForm({
  form: 'teacherRegistrationForm',
  enableReinitialize: true,
  destroyOnUnmount: false,
  validate,
})(TeacherRegistrationPageContainer);

TeacherRegistrationPageContainer = connect(mapStateToProps, mapDispatchToProps)(TeacherRegistrationPageContainer);

export default TeacherRegistrationPageContainer;
