import api from 'services/api';
import * as constants from './constants';
import { groupTasksByDate } from 'utils/helpers';
import {toast} from "react-toastify";


export const fetchStudentClassTasks = classID => {
  return dispatch => {
    dispatch({ type: constants.STUDENT_CLASS_TASKS_FETCH_START });

    return api.fetchStudentClassTasks(classID)
      .then(rsp => {
        let groupedTasks = groupTasksByDate(rsp.data);
        dispatch({ type: constants.STUDENT_CLASS_TASKS_FETCH_SUCCESS, payload: groupedTasks });
      })
      .catch(error => {
        dispatch({ type: constants.STUDENT_CLASS_TASKS_FETCH_FAILURE });
        toast('حدث خطأ في جلب المهام والواجبات، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};


export const fetchStudentClass = classID => {
  return dispatch => {
    return api.fetchStudentClass(classID)
      .then(rsp => {
        dispatch({ type: constants.STUDENT_CLASS_FETCH_SUCCESS, payload: rsp.data });
      })
      .catch(error => {
        toast('حدث خطأ في جلب الصف، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};


export const toggleClassTaskCompletion = taskID => {
  return dispatch => {
    return api.toggleTaskCompletion(taskID)
      .then(rsp => {
        dispatch({
          type: constants.STUDENT_CLASS_TASK_COMPLETION_TOGGLE_SUCCESS,
          payload: rsp.data
        });

        if (rsp.data.completed)
          toast('تم انجاز المهمة بنجاح!', { type: toast.TYPE.SUCCESS });
        else
          toast('تم الغاء اتمامك للمهمة', { type: toast.TYPE.WARNING });
      })
      .catch(error => {
        toast('حدث خطأ في تحديث المهمة، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};
