import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  fetchStudentClasses,
  joinClass
} from 'store/actions';
import StudentClasses from 'components/organisms/StudentClasses/StudentClasses';


class StudentClassesContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isJoinClassModalOpen: false
    };
  }

  componentDidMount() {
    this.props.fetchClasses();
  }

  openJoinClassModalState = () => {
    this.setState({ ...this.state, isJoinClassModalOpen: true });
  }

  closeJoinClassModalState = () => {
    this.setState({ ...this.state, isJoinClassModalOpen: false });
  }

  render() {
    const { isJoinClassModalOpen } = this.state;
    return (
      <StudentClasses
        {...this.props}
        isJoinClassModalOpen={isJoinClassModalOpen}
        openJoinClassModalState={this.openJoinClassModalState}
        closeJoinClassModalState={this.closeJoinClassModalState}
      />
    );
  }
}


const mapDispatchToProps = dispatch => ({
  fetchClasses: () => dispatch(fetchStudentClasses()),
  joinClass: (code, configs={ callback: null }) => dispatch(joinClass(code, configs)),
});

const mapStateToProps = state => ({
  classesLoading: state.studentClasses.classesLoading,
  classes: state.studentClasses.classes,

  joinClassLoading: state.studentClasses.joinClassLoading,
  joinClassError: state.studentClasses.joinClassError
});


StudentClassesContainer = connect(mapStateToProps, mapDispatchToProps)(StudentClassesContainer);

export default StudentClassesContainer;
