import React from 'react';
import PropTypes from 'prop-types';


const ImageIcon = ({ name, width, imgProps, ...props }) => {
  let style = {};
  if (width)
    style['width'] = width;

  return (
    <span className="image-icon-container" {...props}>
      <img
        className="image-icon"
        src={require(`assets/img/icons/icon-${name}.png`)}
        alt={name}
        style={style}
        {...imgProps}
      />
    </span>
  );
};

ImageIcon.propTypes = {
  name: PropTypes.string.isRequired,
  width: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  imgProps: PropTypes.object
};

export default ImageIcon;
