import React from 'react';
import PropTypes from 'prop-types';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import 'components/ConfirmAlert/ConfirmAlert.css';


const TeacherTaskCard = ({ task, showClass, openEditTaskModal, deleteClassTask }) => {
  const { id, text, task_class, completed_count } = task;

  const onTaskEdit = () => openEditTaskModal(id);

  const onTaskDelete = () => {
    confirmAlert({
      title: 'حذف المهمة',
      message: `هل أنت متأكد من أنك تريد حذف هذه المهمة/الواجب؟ (${text.slice(0, 20)}...)`,
      // message: 'هل أنت متأكد من أنك تريد حذف هذه المهمة/الواجب؟',
      buttons: [
        {
          label: 'لا',
          onClick: () => {}
        },
        {
          label: 'نعم',
          onClick: () => deleteClassTask(task_class.id, id)
        }
      ]
    });
  };

  return (
    <div className="card task-card teacher">
      <div className="card-body">

        <div className="task-content-container">
          <label className="checkbox card-checkbox">
            <input type="checkbox" />
            <span className="primary"></span>
          </label>

          <p className="task-content">{text}</p>
        </div>

        <div className="actions">
          <i className="far fa-edit action-icon" data-toggle="tooltip" data-placement="top" title="تعديل المهمة" onClick={onTaskEdit}></i>
          <i className="far fa-trash-alt action-icon color-danger" data-toggle="tooltip" data-placement="top" title="حذف المهمة" onClick={onTaskDelete}></i>
        </div>
      </div>

      <div className="card-footer">
        <p className="students-completed-count">
          عدد الطلاب الذين أنجزوا هذه المهمة: <span className="badge badge-success">{completed_count}</span>
        </p>
      </div>
    </div>
  );
};

TeacherTaskCard.propTypes = {
  task: PropTypes.object.isRequired,
  showClass: PropTypes.bool,
  deleteClassTask: PropTypes.func.isRequired
};

TeacherTaskCard.defaultProps = {
  showClass: false
};

export default TeacherTaskCard;
