import appHistory from 'appHistory';
import queryString from 'query-string'


const nav = (location, { useHistory=true, addCurrentParams=false, extraParams={} } = {}) => {
  let params = { ...extraParams }

  if(addCurrentParams)
      params = {
        ...params,
        ...queryString.parse(window.location.search)
      }

  let queryParams = queryString.stringify(params)
  let newLink = location

  if (useHistory) {
    let historyObj = { pathname: newLink }

    if (queryParams) {
      historyObj["search"] = `?${queryParams}`
    }
    appHistory.push(historyObj);

  } else {
    if (queryParams)
      newLink += `?${queryParams}`

    window.location.href = newLink
  }
}

export default nav
