import React, { Component } from 'react';
import Modal from 'react-modal';
import { isEmpty } from 'lodash';
import { isMobile } from "react-device-detect";
import { reduxForm } from "redux-form";
import { createValidator, required, minLength } from 'utils/validation';
import { connect } from "react-redux";
import { submitClass, hideAlert, showAlert } from 'store/actions';
import { Form, Field } from 'redux-form';
import { FormTextField, FormAlert } from 'components/molecules';


const modalStyle = {
  overlay: {
    backgroundColor: 'rgba(0, 0, 0, 0.4)'
  },
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)',
    padding               : 0,
    width                 : isMobile ? '90%' : '500px',
  }
};


class ClassModal extends Component {
  componentWillUnmount() {
    this.props.hideAlert();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (this.props.error)
      this.props.showAlert(this.props.error, 'danger');

    if (this.props.submitSucceeded)
      this.props.closeModal();
  }

  render() {
    const { handleSubmit, submitting, alert, isOpen, isEditMode, closeModal } = this.props;

    let modalTitle = isEditMode ? 'عدّل الصف' : 'أضف صف جديد';
    let submitButtonText = isEditMode ? 'عدّل الصف' : 'أضف الصف';

    return (
      <Modal
        isOpen={isOpen}
        onRequestClose={closeModal}
        style={modalStyle}
        appElement={document.getElementById('root')}
      >
        <div className="add-class-modal">
          <div className="modal-header">
            <h5 className="modal-title">{modalTitle}</h5>
            <button type="button" className="close" aria-label="Close" onClick={closeModal}>
              <span aria-hidden="true">&times;</span>
            </button>
          </div>

          <div className="modal-body">
            <FormAlert visible={alert.visible} type={alert.type} message={alert.message}/>

            <Form className="form registration-form" onSubmit={handleSubmit}>
              <Field
                component={FormTextField}
                label="اسم الصف"
                name="name"
                id="name"
                autoComplete="name"
                placeholder="مثال: الصف الخامس"
                type="text"
              />

              <Field
                component={FormTextField}
                label="المادة"
                name="subject"
                id="subject"
                autoComplete="subject"
                placeholder="مثال: الرياضيات"
                type="text"
              />

              <Field
                component={FormTextField}
                label="اسم المدرسة"
                name="school"
                id="school"
                autoComplete="school"
                placeholder="مثال: مدارس الرضوان"
                type="text"
              />

              <button type="submit" className="btn btn-primary btn-lg btn-block" disabled={submitting}>
                {submitButtonText}
              </button>
            </Form>
          </div>
        </div>
      </Modal>
    );
  }
}


const validate = createValidator({
  name: [required, minLength(3)],
  subject: [required, minLength(2)],
  school: [required, minLength(3)]
});

const mapDispatchToProps = dispatch => ({
  showAlert: (message, type) => dispatch(showAlert(message, type)),
  hideAlert: () => dispatch(hideAlert()),
  onSubmit: (data, dispatch) => dispatch(submitClass(data))
});


const mapStateToProps = state => {
  let editClass = state.teacherClasses.editClass;
  let isEditMode = false;

  let initialValues = {};

  if (!isEmpty(editClass)) {
    initialValues = editClass;
    isEditMode = true;
  }

  return {
    initialValues,
    isEditMode,
    alert: state.alert
  };
};


ClassModal = reduxForm({
  enableReinitialize: true,
  form: 'classForm',
  destroyOnUnmount: false,
  validate,
})(ClassModal);

ClassModal = connect(mapStateToProps, mapDispatchToProps)(ClassModal);


export default ClassModal;
