import React from 'react';
import PropTypes from 'prop-types';


const StudentTaskCard = ({ task, showClass, toggleTaskCompletion }) => {
  const { id, text, task_class, completed } = task;

  const onCompleteChange = event => toggleTaskCompletion(id);

  return (
    <div className="card task-card student">
      <div className="card-body">

        <div className="task-content-container">
          <label className="checkbox card-checkbox">
            <input type="checkbox" checked={completed} onChange={onCompleteChange} />
            <span className="primary"></span>
          </label>

          <p className="task-content">{text}</p>
        </div>
      </div>

      {showClass ? (
        <div className="card-footer">
          <p className="card-footer-text">
            من مفكرة صف: <span className="font-weight-bold">{task_class.name}</span>
          </p>
        </div>
      ) : null}
    </div>
  );
};

StudentTaskCard.propTypes = {
  task: PropTypes.object.isRequired,
  showClass: PropTypes.bool,
  toggleTaskCompletion: PropTypes.func
};

StudentTaskCard.defaultProps = {
  showClass: false
};

export default StudentTaskCard;
