import React from 'react';
import PropTypes from 'prop-types';
import session from 'services/session';
import StudentTaskCard from './StudentTaskCard';
import TeacherTaskCard from './TeacherTaskCard';


const TaskCard = ({ task, showClass, toggleTaskCompletion,
                    deleteClassTask, openEditTaskModal }) => {
  let isTeacher = session.isTeacher();
  if (isTeacher)
    return <TeacherTaskCard task={task} showClass={showClass} openEditTaskModal={openEditTaskModal} deleteClassTask={deleteClassTask} />;

  return <StudentTaskCard task={task} showClass={showClass} toggleTaskCompletion={toggleTaskCompletion} />;
};

TaskCard.propTypes = {
  task: PropTypes.object.isRequired,
  showClass: PropTypes.bool,
  toggleTaskCompletion: PropTypes.func
};

TaskCard.defaultProps = {
  showClass: false
};

export default TaskCard;
