import { routes, nav } from 'router';


export const redirectToTeacherRegistration = () => nav(routes.teacherRegistration);
export const redirectToStudentRegistration = () => nav(routes.studentRegistration);

export const redirectToStudentLogin = () => nav(routes.studentLogin);
export const redirectToTeacherLogin = () => nav(routes.teacherLogin);
export const redirectToLogin = redirectToStudentLogin;
