import React, { Component } from 'react';
import { reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { createValidator, required, minLength, match, firstAndLastName } from 'utils/validation';
import { registerStudent, showAlert, hideAlert } from 'store/actions';
import StudentRegistrationPage from 'pages/StudentRegistrationPage/StudentRegistrationPage';


class StudentRegistrationPageContainer extends Component {
  componentWillUnmount() {
    this.props.hideAlert();
  }

  componentDidUpdate(prevProps, prevState, snapshot){
    if (this.props.error)
      this.props.showAlert(this.props.error, 'danger');
  }

  render() {
    return (
      <StudentRegistrationPage {...this.props} />
    );
  }
}


const validate = createValidator({
  username: [required, minLength(2)],
  name: [required, minLength(2), firstAndLastName],
  password: [required, minLength(5)],
  password_confirmation: [required, match('password')]
});

const mapDispatchToProps = dispatch => ({
  showAlert: (message, type) => dispatch(showAlert(message, type)),
  hideAlert: () => dispatch(hideAlert()),
  onSubmit: (data, dispatch) => dispatch(registerStudent(data))
});

const mapStateToProps = state => ({
  initialValues: {},
  alert: state.alert
});


StudentRegistrationPageContainer = reduxForm({
  form: 'studentRegistrationForm',
  enableReinitialize: true,
  destroyOnUnmount: false,
  validate,
})(StudentRegistrationPageContainer);

StudentRegistrationPageContainer = connect(mapStateToProps, mapDispatchToProps)(StudentRegistrationPageContainer);

export default StudentRegistrationPageContainer;
