import React from 'react';
import { Form, Field } from 'redux-form';
import { FormTextField, FormAlert } from 'components/molecules';
import { LoadingSpinner } from 'components/atoms';


const TeacherRegistrationForm = ({ handleSubmit, submitting, alert }) => {
  return (
    <Form className="form registration-form" onSubmit={handleSubmit}>
      <FormAlert visible={alert.visible} type={alert.type} message={alert.message}/>

      <Field
        component={FormTextField}
        className="form-control-lg"
        label="البريد الإلكتروني"
        name="email"
        id="email"
        placeholder="مثال: omar.najjar@gmail.com"
        type="email"
        autoComplete="email"
      />

      <Field
        component={FormTextField}
        className="form-control-lg"
        label="الاسم الثنائي"
        name="name"
        id="name"
        placeholder="مثال: عمر النجار"
        helpText="يرجى كتابة اسمك الأول واسم عائلتك"
        type="text"
        autoComplete="name"
      />

      <Field
        component={FormTextField}
        className="form-control-lg"
        label="اسم المستخدم"
        name="username"
        id="username"
        autoComplete="username"
        placeholder="مثال: omar_najjar"
        helpText="سوف تستخدمه عند تسجيل الدخول في المرات القادمة"
        type="text"
      />

      <Field
        component={FormTextField}
        className="form-control-lg"
        label="كلمة السر"
        name="password"
        id="password"
        type="password"
      />

      <Field
        component={FormTextField}
        className="form-control-lg"
        label="تأكيد كلمة السر"
        helpText="يرجى إعادة إدخال كلمة السر مرة أخرى للتأكيد"
        name="password_confirmation"
        id="password_confirmation"
        type="password"
      />

      {submitting ? (
        <>
          <LoadingSpinner />
          <br/>
        </>
      ) : null}

      <button type="submit" className="btn btn-primary btn-lg btn-block" disabled={submitting}>سجّل</button>
    </Form>
  );
};

export default TeacherRegistrationForm;
