import api from 'services/api';
import * as constants from './constants';
import { fetchStudentTasksToday } from 'store/actions';
import {toast} from "react-toastify";


export const fetchStudentClasses = () => {
  return dispatch => {
    dispatch({ type: constants.STUDENT_CLASSES_FETCH_START });

    return api.fetchStudentClasses()
      .then(rsp => {
        dispatch({ type: constants.STUDENT_CLASSES_FETCH_SUCCESS, payload: rsp.data });
      })
      .catch(error => {
        dispatch({ type: constants.STUDENT_CLASSES_FETCH_FAILURE });
        toast('حدث خطأ في جلب الصفوف، الرجاء المحاولة لاحقاً', { type: toast.TYPE.ERROR });
      });
  };
};


export const joinClass = (code, configs = { callback: null }) => {
  return dispatch => {
    dispatch({ type: constants.CLASS_JOIN_START });

    return api.joinClass(code.trim())
      .then(rsp => {
        dispatch({ type: constants.CLASS_JOIN_SUCCESS, payload: rsp.data });
        if (configs.callback)
          configs.callback();

        toast('تم الانضمام للصف بنجاح', { type: toast.TYPE.SUCCESS });

        dispatch(fetchStudentClasses());
        dispatch(fetchStudentTasksToday());
      })
      .catch(error => {
        if (error.response && error.response.status === 404)
          dispatch({ type: constants.CLASS_JOIN_FAILURE, payload: 'الرمز غير صحيح، تأكد من إدخالك الرمز الصحيح' });
        else
          dispatch({ type: constants.CLASS_JOIN_FAILURE, payload: 'حدث خطأ غير متوقع، الرجاء المحاولة لاحقاً' });
      });
  };
};
