import * as constants from './constants';


const INITIAL_STATE = {
  class_: {},
  classTasksLoading: false,
  classTasksByDate: {}
};


const updateTaskCompletion = (classTasksByDate, task) => {
  let _tasksByDate = {...classTasksByDate};
  let taskIndex = _tasksByDate[task.date].findIndex((obj => obj.id === task.id));
  _tasksByDate[task.date][taskIndex] = task;
  return _tasksByDate;
};


export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case constants.STUDENT_CLASS_FETCH_SUCCESS:
      return { ...state, class_: payload };


    case constants.STUDENT_CLASS_TASKS_FETCH_START:
      return { ...state, classTasksLoading: true };

    case constants.STUDENT_CLASS_TASKS_FETCH_SUCCESS:
      return { ...state, classTasksLoading: false, classTasksByDate: payload };

    case constants.STUDENT_CLASS_TASKS_FETCH_FAILURE:
      return { ...state, classTasksLoading: false };


    case constants.STUDENT_CLASS_TASK_COMPLETION_TOGGLE_SUCCESS:
      return { ...state, classTasksByDate: updateTaskCompletion(state.classTasksByDate, payload) };

    default:
      return state;
  }
}
