import React from 'react';
import { LoadingSpinner, PlaceholderText, ImageIcon } from 'components/atoms';
import { DateTasks } from 'components/molecules';


const TeacherClassTasks = ({ classTasksLoading, classTasksByDate, openTaskModal, openEditTaskModal,
                             deleteClassTask }) => {
  let datesTasks = Object.entries(classTasksByDate).map(([date, tasks], index) => (
    <DateTasks
      key={`${date}_${index}`}
      date={date}
      tasks={tasks}
      showTitle={true}
      showClass={false}
      deleteClassTask={deleteClassTask}
      openEditTaskModal={openEditTaskModal}
    />
  ));

  return (
    <div className="page-content-block container">
      <div className="tasks">
        <h4 className="block-title text-right"><ImageIcon name="task" />المهام / الواجبات
          <button
            type="button"
            className="btn btn-outline-primary btn-add-class"
            onClick={openTaskModal}>
            أضف مهمة جديدة
          </button>
        </h4>

        <div className="tasks-container">
          <div className="row">
            {classTasksLoading ? (
              <LoadingSpinner large />
            ) : (
              datesTasks.length === 0 ? (
                <PlaceholderText text='لا يوجد مهام/واجباب في هذا الصف حتى الآن!' />
              ) : (
                datesTasks
              )
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default TeacherClassTasks;
