import React from 'react';
import PropTypes from 'prop-types';
import Datetime from "react-datetime";
import 'react-datetime/css/react-datetime.css';
import './react-datetime.css';


const isValidDate = (currentDate, selectedDate) => {
  let monthBefore = Datetime.moment().subtract(1, 'month');
  let monthAfter = Datetime.moment().add(1, 'month');
  return currentDate.isAfter(monthBefore) && currentDate.isBefore(monthAfter);
};


const FormDatetimeField = ({ label, id, helpText, defaultValue, timeFormat, isFullWidth, className,
                             meta, input, children }) => {
  let errorExists = meta && ( meta.touched || meta.active ) && meta.error;

  return (
    <div className={`form-group form-text-field ${ errorExists ? 'has-error' : ''} ${isFullWidth ? 'full-width' : ''}`}>
      {label ? (
        <label className="control-label" htmlFor={id}>{label}</label>
      ) : null }

      {helpText ? (
        <small className="form-text text-muted">{helpText}</small>
      ) : null }

      <Datetime
        className={`task-date-picker ${className}`}
        open={true}
        timeFormat={timeFormat}
        defaultValue={defaultValue}
        value={input.value}
        onChange={input.onChange}
        isValidDate={isValidDate}
        inputProps={{
          id: id,
          ...input
        }}
      />

      {children}

      { errorExists ? (
        <span id={`${id}-error`} className="invalid-feedback">{meta.error}</span>
      ) : null}
    </div>
  )
};


FormDatetimeField.propTypes = {
  label: PropTypes.string,
  id: PropTypes.string.isRequired,
  helpText: PropTypes.string,
  isFullWidth: PropTypes.bool,
  meta: PropTypes.shape({
    touched: PropTypes.bool,
    error: PropTypes.string
  }),
  input: PropTypes.shape({
    name: PropTypes.string.isRequired,
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.object])
  }),
  defaultValue: PropTypes.object,
  timeFormat: PropTypes.bool
};


FormDatetimeField.defaultProps = {
  isFullWidth: true,
  timeFormat: true,
  defaultValue: new Date(),
};


export default FormDatetimeField;
